<template>
  <div class="bac" :style="heiObj">


    <div class="right_bac">
      <div class="right_list">
        <!--右上角的相邻文章-->
        <div class="text" @click="turnRight('pre')">
          <div class="text_img_div">

          </div>
          <div class="text_text">
            {{content.pre.title}}
          </div>
        </div>

        <div class="text" @click="turnRight('next')">
          <div class="text_img_div">

          </div>
          <div class="text_text">
            {{content.next.title}}
          </div>
        </div>




      </div>
    </div>



    <!--显示的正篇文章-->
    <div class="main">
      <div class="title">
        <a class="title_word">{{content.res.title}}</a>
      </div>
      <div class="content">
        <a class="content_word" v-html="content.res.content">
          {{content.res.content}}
        </a>
      </div>
      <div class="time">
        <a class="time_">{{content.res.create_time}}</a>
      </div>
    </div>




  </div>
</template>

<script>

  export default {

    name: 'third_page',
    inject:['turnTo','requestTo'],
    data(){
      return{
        content:[], // 获取到的三篇文章内容
        heiObj:{
          height:'',
          fontSize:''
        },
      }
    },

    methods:{
      getHeight(){
        this.heiObj.height = window.innerHeight + 'px';
        this.heiObj.fontSize = window.innerWidth / 90 + 'px'
      },
      async turnRight(whe){
        console.log(whe);
        let res;
        if(whe === 'pre'){
          const contentId = {pid : this.content.pre.id};
          res = await this.requestTo('/get_content',contentId);
        } else {
          const contentId = {pid : this.content.next.id};
          res = await this.requestTo('/get_content',contentId);
        }
        this.content = res.data.data;
      },
      async requestIn (contentId) {  // 请求文章
        return await this.requestTo('/get_content',contentId);
      }
    },

    async created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();

      let STo = this.$store.state;
      const contentId = {pid : STo.content_id};

      const res = await this.requestIn(contentId);   // 三级页文章请求
      this.content = res.data.data;

    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }
  };
</script>






<style scoped>
  .top{
    z-index: 9;
  }
  .mar{
    margin: auto;
  }


  .bac{
    background: url("../../../../assets/wood.jpg") no-repeat center fixed;
    background-size: cover;
    overflow: auto;
  }
  .bac::-webkit-scrollbar{
    width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 5px;
  }
  .bac::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: rgba(83, 83, 83, 0.5);
  }
  .bac::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 0;
    background: rgba(0,0,0,0.1);
  }







  .right_bac{
    position: relative;
    height: 0;
    width: 100%;
    z-index: 3;
  }
  .right_bac:hover{
    z-index: 19;
  }
  .right_list{
    text-align: center;
    width: 36em;
    margin: 0 0 auto auto;
    padding-top: 4em;
    /*background-color: #007aff;*/
  }
  .text{
    display: block;
    height: 15em;
    width: 31em;
    margin: 3em auto;
    padding-top: 1.3em;
    background-color: white;
    box-shadow: 0 0 14px #858585;
  }
  .text:hover{
    transition: height 240ms,width 240ms;
    height: 16.4em;
    width: 31.775em;
    box-shadow: 0 0 14px rgba(251, 87, 31, 0.8);
    cursor: pointer;
  }
  .text_img_div{
    /*height: 10em;*/
    height: 70%;
    /*width: 26em;*/
    width: 86%;
    margin: 0 auto 0.5em auto;
    background-color: rgba(136, 136, 136, 0.96);
  }
  .text_text{
    height: 4em;
    width: 26em;
    margin: 0 auto;
    /*background-color: #007aff;*/
  }







  .main{
    position: relative;
    /*display: flex;*/
    text-align: center;
    width: 65em;
    margin: 210px auto 100px 6em;
    background-color: white;
    box-shadow: 0 0 24px #252525;
    z-index: 9;
  }
  .title{
    display: flex;
    height: 10em;
    width: 94%;
    margin: 0 auto;
    text-align: center;
    border-bottom: 1px solid #cccccc;
  }
  .title_word{
    color: #356ACA;
    font-weight: bolder;
    font-size: 1.9em;
    margin: 1.6em auto auto auto;
  }

  .content{
    width: 94%;
    text-align: left;
    margin: 1.1em auto 0 auto;
  }
  .content_word{
    font-size: 14px;
  }
  .time{
    height: 40px;
    width: 94%;
    margin: 1em auto 1em auto;
    text-align: right;
  }

  .time_{
    margin: auto 1em auto auto;
    color: #356ACA;
  }






</style>



