<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
  </div>
</template>


<!--项目用红色: rgb(251, 87, 31)-->
<!--透明度: 0.8-->

<!--项目用黑色: rgba(0, 0, 0, 0.4)-->





<script>
  import axios from 'axios';

  export default {
    name: 'App',
    provide() {
      return {
        reload: this.reload,
        requestTo: this.requestTo,
        turnTo: this.turnTo,
      };
    },
    data() {
      return {
        isRouterAlive: true,
      };
    },

    methods: {
      // 重载封装
      reload() {
        this.isRouterAlive = false;
        this.$nextTick(() => (this.isRouterAlive = true));
      },

      //浏览器返回
      goBack(){
        const B = this.$store.state._back;
        this.turnTo(B.back_select,B.back_num,B.back_display)
      },

      // 请求封装
      requestTo (Tail,data) {
        return new Promise((resolve) => {
          axios.get(Tail, {
            params: data
          })
            .then(resp => {
              if (resp.status >= 300) {
                console.error('网络错误', resp);
                resolve(resp);
              }
              resolve(resp);
            })
            .catch(function (error) {
              console.log(error);
            });
        });
      },

      // 跳转封装
      turnTo (from = this.$store.state.lab_select,index = this.$store.state.lab_num,to) {  // from是行   index是列   to是跳转到
        let STo = this.$store.state;
        // if (STo.lab_select !== from || STo.lab_num !== index) {
          // 记录上一个状态
          STo._back = { back_display: STo.display, back_select: STo.lab_select, back_num: STo.lab_num };
          // 刷新当前状态
          STo.lab_select = from;
          STo.lab_num = index;
          STo.display = to;

          this.reload();    // 重载页面，用于刷新组件
        // }

      }


    },

    mounted(){
      // 监听返回
      if (window.history && window.history.pushState) {
        history.pushState(null, null, document.URL);
        window.addEventListener('popstate', this.goBack, false);
      }
    },
    destroyed(){
      window.removeEventListener('popstate', this.goBack, false);   // 销毁监听
    },


  };


</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
    padding: 0;
  }

</style>



