import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


const store = new Vuex.Store({
  // 定义状态!只能写在这里!!
  state: {
    showCss: true, // 首页刚进入时的动画，当第一次打开时为true即播放，之后回到首页就不需要播放了一直是false

    display: 'Home', // 用于填充页面的组件显示情况
    lab_select: 0, // 选择的哪个header列 0为首页
    lab_num: 0, // 选择header列的哪一个

    content_id: 0, // 选择的文章列表页文章ID

    _back:{  // 用来做浏览器回退的，存储了上一个状态
      back_display:'home',   // 上一个display
      back_select: 0,   // 上一个选择的行
      back_num: 0,  // 上一个选择的列
    },

    lab:{  // 用于填充leftBar
      lab_1:['公司简介','品牌介绍','组织架构','企业文化','荣誉认证'],
      lab_2:['行业动态','公司新闻'],
      lab_3:['中央精神','党建要闻','支部状态','党建专题'],
      lab_4:['业务总览','赛事平台','国际公益','出版物','技术推广'],
      lab_5:['人才团队','员工风采','场地风采','政府合作','招商合作'],
      lab_6:['企业相册','企业视频'],
      lab_7:['联系方式']
    },
  },

});

export default store







