<template>
  <div class="bac" :style="heiObj">
    <div class="Frame">
      <img src="../../../../assets/share.gif" class="img_part">
      <img src="../../../../assets/book.gif" class="img_part">
      <img src="../../../../assets/money.gif" class="img_part">
      <img src="../../../../assets/laod.gif" class="img_part">
    </div>

    <div class="Frame">
      <img src="../../../../assets/cpu.gif" class="img_part">
      <img src="../../../../assets/wx.gif" class="img_part">
      <img src="../../../../assets/up.gif" class="img_part">
      <img src="../../../../assets/cheers.gif" class="img_part">
    </div>

    <div class="Frame">
      <img src="../../../../assets/data.gif" class="img_part">
      <img src="../../../../assets/shop.gif" class="img_part">
      <img src="../../../../assets/flip.gif" class="img_part">
      <img src="../../../../assets/computer.gif" class="img_part">
    </div>



  </div>
</template>

<script>

  export default {

    name: 'Job_All',
    inject:['turnTo'],
    data(){
      return{
        heiObj:{
          height:'',
          fontSize:''
        },
      }
    },

    methods:{
      getHeight(){
        this.heiObj.height = window.innerHeight + 'px';
        this.heiObj.fontSize = window.innerWidth / 90 + 'px'
      },

    },

    created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();
    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }
  };
</script>






<style scoped>
  .top{
    z-index: 9;
  }
  .mar{
    margin: auto;
  }


  .bac{
    /*background: url("../../../../assets/wall.jpg") no-repeat center fixed;*/
    background-size: cover;
    overflow: auto;
  }
  .bac::-webkit-scrollbar{
    width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 5px;
  }
  .bac::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: rgba(83, 83, 83, 0.5);
  }
  .bac::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 0;
    background: rgba(0,0,0,0.1);
  }


  .Frame{
    display: flex;
    height: 33%;
    width: 100%;
    /*background-color: #007aff;*/
  }

  .img_part{
    margin: auto;
    height: 180px;
    width: 300px;
  }







</style>



