<template>


  <div class="swiper-container" :style="conObj">
    <div class="swiper-wrapper">

      <div class="swiper-slide" data-swiper-autoplay="5000" v-cloak>
        <div class="div a">
          <div ref="normal" class="w1" v-show="showX">
            <div class="w1_top">
              <a style="margin: auto">中汽研 (北京) 汽车文化有限公司</a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">从2006年开始涉足汽车文化领域，借鉴国际先进汽车文化理论、研究总结中国汽车文化特色，积极推进汽车文化活动实践并形成了大量平台类项目，2018年 中汽中心全资成立了“中汽研(北京)汽车文化有限公司”，它将充分依托中汽中心行业优势，打造汽车行业品牌管理第一智库，承担起中汽中心汽车文化板块相关业务未来发展的重任，为中国汽车文化的整体发展贡献一份力量</a>
            </div>
            <div class="w1_button" @click="turn(from = 10,index = 0,to = 'Job_All')" >
              <a style="margin: auto">查看更多</a>
            </div>
          </div>
          <div ref="show" class="show" v-show="!showX">
            <div ref="show_con" class="show_con">
              <img src="../../../../../static/logo.png" alt="logo" class="show_con_img"/>
            </div>
          </div>
        </div>
      </div>

      <div class="swiper-slide">
        <div class="div b">
          <div class="w1">
            <div class="w1_top w1_top_b">
              <a style="margin: auto">天津之眼</a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">像是奔驰车标落人间，直径110米，28分钟转一圈，无人机航拍的天津市红桥区永乐桥上的“天津之眼”，曾被誉为世界上唯一一座建在桥上的摩天轮。很多人看了脱口而出：这不就是奔驰汽车的标志——方向盘嘛！“天津之眼”下面，就是天津人民的母亲河——海河。</a>
            </div>
            <div class="w1_button" @click="OutSide">
              <a style="margin: auto">查看更多</a>
            </div>
          </div>
        </div>
      </div>

      <div class="swiper-slide">
        <div class="div c">
          <div class="w1">
            <div class="w1_top w1_top_b">
              <a style="margin: auto">赛事项目</a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">于2012年创办的 中国全地形车锦标赛 是目前国内唯一一个全地形车国家级赛事;&nbsp;&nbsp;&nbsp;2015年由中汽研于国家体育总局联合推出了国内首个量产车性能大赛 CCPC;&nbsp;&nbsp;&nbsp;2018年，在上海和三亚两地举办了多厂商最受关注的新能源汽车比赛 CEVC ，是中国新能源汽车行业规模最大，影响力最强的赛事</a>
            </div>
            <div class="w1_button" @click="turn(from = 10,index = 2,to = 'Car_match')">
              <a style="margin: auto" >查看更多</a>
            </div>
          </div>
        </div>
      </div>
    </div>


  </div>

</template>



<script>
  import Swiper from "swiper"

  export default {
    name: 'Home_page',
    inject:['turnTo'],
    data(){
      return{
        conObj:{
          height:''
        },
        showX: false
      }
    },
    methods:{
      async show_x(){ // 进入首页的时候的动画
        this.$store.state.showCss = false;
        let that = this;
        this.$refs.show_con.style.transition = 'height 500ms,width 600ms,border-color 600ms,opacity 300ms';
        this.$refs.show_con.style.height = '162px';
        setTimeout(function(){
          that.$refs.show_con.style.width = '312px';
          that.$refs.show_con.style.borderColor = 'rgba(255,255,255,0)';
        }, 500);
        setTimeout(function(){
          that.$refs.show_con.style.opacity = '0';
        }, 1500);
        setTimeout(function(){
          that.showX = !that.showX
        }, 1800);
      },
      getHeight(){
        this.conObj.height = window.innerHeight + 'px';
      },
      OutSide(){
        window.location.href="https://baike.baidu.com/item/%E5%A4%A9%E6%B4%A5%E4%B9%8B%E7%9C%BC/10021404?fr=aladdin";
      },
      turn (from,index,to){  // 依据所选header项载入二级页面
        this.turnTo(from,index,to);
      }
    },
    mounted() {
      var mySwiper = new Swiper('.swiper-container', {
        direction: 'vertical',
        mousewheel: true,
        loop: false,
        autoplay: {
          delay: 3500,
        },
        pagination: {
          el: '.swiper-pagination',
        },
      })
    },
    created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();
      let that = this;
      if(this.$store.state.showCss){
        setTimeout(function(){
          that.show_x()
        }, 700);
      } else {
        this.showX = true
      }
    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }

  };
</script>




<style scoped>

  .swiper-container{
    width: 100%;
  }
  .div{
    height: 100%;
    width: 100%;
    padding-top: 15%;
  }




  .show{
    display: flex;
    height: 160px;
    width: 100%;
    /*background-color: #c319ff;*/
  }
  .show_con{
    display: flex;
    height: 0;
    width: 0;
    margin: auto;
    border-right: 2px solid white;
    border-left: 2px solid white;
    border-radius: 2px;
    overflow: hidden;
  }
  .show_con_img{
    height: 160px;
    width: 310px;
    margin: auto;
  }




  .a{
    background: url("../../../pc/img/a.jpg") no-repeat center fixed;
    background-size:cover;
  }
  .b{
    background: url("../../../pc/img/b.jpg") no-repeat center;
    background-size:cover;
  }
  .c{
    background: url("../../../pc/img/c.jpg") no-repeat center;
    background-size:cover;
  }
  .w1{
    height: 300px;
    width: 800px;
    margin: 0 16%;
  }
  .w1_top{
    display: flex;
    height: 60px;
    width: 545px;
    color: white;
    font-size: 36px;
    font-weight: bolder;
    background: rgba(0,0,0,0.4);
  }
  .w1_top_b{
    width: 180px;
  }
  .w1_mid{
    display: flex;
    height: 100px;
    width: 100%;
    margin-top: 40px;
    padding: 0 26px;
    font-size: 14px;
    font-weight: bolder;
    color: white;
    background: rgba(0,0,0,0.4);
  }
  .w1_button{
    display: flex;
    height: 30px;
    width: 90px;
    margin-top: 40px;
    color: white;
    border: 1px solid rgba(251, 87, 31, 0);
    font-weight: bolder;
    box-shadow: 0 0 10px rgba(251, 87, 31, 0.8);
    background-color: rgba(251, 87, 31, 0.8);
  }
  .w1_button:hover{
    cursor: pointer;
    box-shadow: 0 0 10px rgba(251, 87, 31, 1);
    background-color: rgba(251, 87, 31, 1);
  }
</style>



