<template>
 <div>

   <div id="photo" class="head_view" :style="{backgroundImage:'url(' + headImg + ')'}" >
     <a style="margin: auto;color: white;font-size: 40px;font-weight: bolder">{{headWord}}</a>
   </div>


   <div class="comment_view" :style="heiObj">

     <!--leftBar-->
     <div id="left" class="left_bar">
       <div class="occupy"></div>

       <div class="left_frame" v-for="(value , index) in lab_x" @click="turn(index)">
         <a class="mar">{{value}}</a>
       </div>
     </div>


     <div class="comment_word">
       <div class="mid">
         <div class="mid_head" :style="{backgroundImage:'url(' + headImg + ')'}">
           <a class="mar">{{title}}</a>
         </div>
         <div class="mid_frame" v-for="(value , index) in lab_list" @click="turnThird(index)">
           <a class="cut">{{value.title}}</a>
           <a style="margin: auto 15px auto auto">{{value.create_time}}</a>
         </div>

       </div>

     </div>

   </div>




 </div>
</template>

<script>
  import RED from '../../../../assets/red.png'
  import CAR from '../../../pc/img/a.jpg'
  import SNOW from  '../../../pc/img/c.jpg'

  export default {
    name: 'Twice',
    inject:['turnTo','requestTo'],
    data(){
      return{
        lab_x:[], // leftBar里的数据
        lab_list:[], // list里的数据
        title: '', // 标题
        heiObj:{
          height:''
        },
        headImg: CAR,
        headWord: '中汽研',
      }
    },
    methods:{
      getHeight(){
        this.heiObj.height = window.innerHeight - 250 + 'px';
      },
      daDa(form,index) { // 填充leftBar里的数据和头图
        this.lab_x = this.$store.state.lab['lab_' + form];
        this.title = this.lab_x[index];

        switch (form) {
          case 2:    //党建
            this.headImg = SNOW;
            this.headWord = '这是第二';
            break;
          case 3:    //党建
            this.headImg = RED;
            this.headWord = '';
            break;
        }
      },
      turn(index) {
        this.turnTo(this.$store.state.lab_select,index,'Twice');  // 跳转到二级页
      },
      turnThird(index){ // 点击某一篇文章将要跳转文章页
        this.$store.state.content_id = this.lab_list[index].id;
        this.turnTo(this.$store.state.lab_select,this.$store.state.lab_num,'Third');  // 跳转到三级页
      },
      async requestIn (fateId) { // 请求文章列表
        return await this.requestTo('/index',fateId);
      }
    },

    async created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();
      this.daDa(this.$store.state.lab_select,this.$store.state.lab_num);

      let STo = this.$store.state;
      const num = STo.lab_select * 10 + STo.lab_num;
      const fateId = {type_id : num};
      if( STo.lab_select !== 0 && !(num.toString()).match(/(40|41|90)/i) ){   // 请求白名单  90以上是测试端口
        const res = await this.requestIn(fateId);   // 二级页面新闻列请求
        this.lab_list = res.data.data;
      }

    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight);
    }
  };
</script>






<style scoped>

  .head_view{
    display: flex;
    height: 244px;
    width: 100%;
    background-color: #7d7d7d;
    box-shadow: 0 0 30px #969696;

    background: no-repeat center;
    background-size:cover;
  }
  .comment_view{
    display: flex;
    width: 100%;
    text-align: center;
  }
  .left_bar{
    text-align: center;
    height: 100%;
    width: 15%;
    float: left;
    margin: 0 0 auto auto;
    overflow: auto;
  }
  .left_bar::-webkit-scrollbar{
    width: 0;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 0;
  }


  .left_frame{
    display: flex;
    height: 30px;
    width: 80%;
    margin: 20px auto;
    color: white;
    font-weight: bolder;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  }
  .left_frame:hover{
    cursor: pointer;
    transition: box-shadow 200ms ,background-color 200ms;
    background-color: rgba(251, 87, 31, 0.9);
    box-shadow: 0 0 20px rgba(251, 87, 31, 0.9);
  }
  .occupy{
    height: 30px;
  }
  .mar{
    margin: auto;
  }
  .comment_word{
    text-align: center;
    height: 100%;
    width: 60%;
    margin: 0 auto auto 0;
    float: left;
    padding: 0;
    overflow: auto;
  }
  .comment_word::-webkit-scrollbar{
    width: 0;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 0;
  }



  .mid{
    width: 90%;
    margin: 50px auto ;
    box-shadow: 0 0 15px #969696;
    /*border-radius: 10px;*/
    overflow: hidden;
  }
  .mid_head{
    display: flex;
    height: 50px;
    width: 100%;
    font-size: 26px;
    font-weight: bolder;
    color: white;
    background-color: #969696;
  }
  .mid_frame{
    display: flex;
    height: 35px;
    /*text-align: left;*/
    border-bottom: 1px solid #cccccc;
  }
  .mid_frame:hover{
    cursor: pointer;
    background-color: #d9d9d9;
    color: rgb(251, 87, 31);
  }
  .cut{
    width: 70%;
    margin: auto 20px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }


</style>



