<template>
  <div ref="foot" class="foot" @mouseover="hov(1)" @mouseout="hov(0)">
    <div ref="up" class="up">
      <img src="../../../../../static/up_white.png" alt="up" class="up_img">
    </div>
    <div class="foot_con">
      <div class="foot_logo">
        <img src="../../../../../static/logo.png" alt="logo" class="foot_img" draggable="false"/>
      </div>
      <div class="foot_list">
        <div class="foot_list_frame">
          <div class="foot_list_frame_div"><a class="mar">创新项目平台</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">电子竞技平台</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">行业评价平台</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">电视活动平台</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">精准营销案例</a></div>
        </div>
        <div class="foot_list_frame">
          <div class="foot_list_frame_div"><a class="mar">比赛赛事</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">CCPC</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">CEVC</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">CATV</a></div>
        </div>
        <div class="foot_list_frame">
          <div class="foot_list_frame_div"><a class="mar">党建活动</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">中央精神</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">党建要闻</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">支部状态</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">党建专题</a></div>
        </div>
        <div class="foot_list_frame">
          <div class="foot_list_frame_div"><a class="mar">最新动态</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">行业动态</a></div>
          <div class="foot_list_frame_div foot_but"><a class="mar">公司新闻</a></div>
        </div>
        <div class="foot_list_frame">
          <div class="foot_list_frame_div"><a class="mar">联系我们</a></div>
          <div class="erweima">
            <img :src="two_url" style="margin: auto;height: 81%;width: 81%" draggable="false">
          </div>
        </div>

      </div>

    </div>
    <div class="supplier">
      <div class="supplier_text"><a class="mar">合作链接</a></div>
      <div class="sup_frame" style="width: 50px">
        <img src="../../../../assets/catarc.png" style="height: 100%" alt="catarc" class="mar" draggable="false"/>
      </div>
      <div class="sup_frame" style="width: 50px">
        <div class="cir">
          <img src="../../../../assets/gxever.png" style="width: 85%" alt="catarc" class="mar" draggable="false"/>
        </div>

      </div>
      <div class="sup_frame" style="width: 72px">
        <img src="../../../../assets/asac.png" style="height: 88%" alt="catarc" class="mar" draggable="false"/>
      </div>
      <div class="sup_frame">
        <img src="../../../../assets/ccpc.png" style="height: 87%" alt="catarc" class="mar" draggable="false"/>
      </div>
      <div class="sup_frame">
        <img src="../../../../assets/cevc.png" style="height: 92%" alt="catarc" class="mar" draggable="false"/>
      </div>
      <div class="sup_frame" style="width: 60px">
        <img src="../../../../assets/catv.png" style="height: 100%" alt="catarc" class="mar" draggable="false"/>
      </div>

    </div>
  </div>
</template>

<script>
  import TWO from '../../../../assets/two.png'

  export default {
    name: 'Footer',
    inject:['turnTo'],
    data () {
      return {
        two_url: TWO
      }
    },
    methods:{
      hov(from){
        if (from) {
          this.$refs.foot.style.height = '310px';
          this.$refs.up.style.opacity = '0'
        } else {
          this.$refs.foot.style.height = '0';
          this.$refs.up.style.opacity = '1'
        }
      },
      turn (from,index,to){  // 依据所选header项载入二级页面
        this.turnTo(from,index,to);
      },


    },


  };
</script>

<style scoped>

  .mar{
    margin: auto;
  }
  .up{
    display: flex;
    height: 60px;
    width: 120px;
    margin: -60px auto 0 auto;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    transition: opacity 300ms;
    background-color: rgba(0,0,0,0.5);
  }
  .up_img{
    height: 80px;
    width: 80px;
    margin: -8px auto 0 auto;
  }
  .foot{
    position: fixed;
    bottom: 0;
    height: 0;
    width: 100%;
    padding-top: 1px;
    background-color: rgba(0,0,0,0.5);
    transition: height 300ms;
  }

  .foot_con{
    display: flex;
    height: 200px;
    width: 1360px;
    margin: 30px auto 0 auto;
  }
  .supplier{
    display: flex;
    height: 30px;
    width: 1180px;
    margin: 24px auto 0 auto;
    /*background-color: #f57740;*/
  }
  .foot_logo{
    display: flex;
    height: 200px;
    width: 400px;
    margin: 0 0 0 auto;
    border-right: 2px solid white;
  }
  .foot_img{
    height: 123px;
    width: 240px;
    margin: auto 40px auto auto;
  }

  .foot_list{
    display: flex;
    height: 200px;
    width: 950px;
    padding-left: 6px;
    margin: 0 auto 0 0;
    color: white;
    /*background-color: aqua;*/
  }
  .foot_list_frame{
    height: 200px;
    width: 150px;
    margin: auto 0;
  }
  .foot_list_frame_div{
    display: flex;
    height: 40px;
    width: 100%;
    font-weight: bolder;
    font-size: 16px;
    margin: 0 auto;
  }
  .foot_but{
    font-size: 13px;
  }
  .foot_but:hover{
    cursor: pointer;
    transition: box-shadow 200ms,background-color 200ms;
    box-shadow: 0 0 20px rgba(251, 87, 31, 0.8);
    background-color: rgba(251, 87, 31, 0.8);
  }

  .erweima{
    display: flex;
    height: 150px;
    width: 100%;
    margin: 0 auto;
    /*background-color: white;*/
  }


  .supplier_text{
    display: flex;
    height: 100%;
    width: 65px;
    margin: auto 0 auto 10px;
    font-size: 14px;
    font-weight: bolder;
    color: white;
  }
  .sup_frame{
    display: flex;
    height: 80%;
    width: 90px;
    margin: auto 12px;
    /*background-color: rgba(255, 255, 255, 0.3);*/
  }
  .sup_frame:hover{
    cursor: pointer;
  }
  .cir{
    display: flex;
    height: 100%;
    width: 24px;
    border-radius: 50px;
    margin: auto;
    background-color: white
  }
</style>



