<template>
  <div class="car_bac" :style="{height:height,background: 'url('+ background +') no-repeat center center fixed',backgroundSize: size }">
    <div class="background">

      <div class="car_frame" style="margin: auto 0 auto auto" @mouseover="banner(show = 1)">
        <div ref="frame_1" class="frame_border frame">
          <div class="hover_div">
            <div class="hover_div_img">
              <img ref="img_1" draggable="false" src="../../../../assets/ccpc.png" alt="ccpc" class="hover_img" />
            </div>
            <div class="hover_mid">
              <a ref="word_1" class="mar">中国量产车性能大赛</a>
            </div>
            <div ref="more_1" class="more">
              <a class="mar">了解更多</a>
            </div>
          </div>
        </div>
      </div>
      <div class="car_frame" style="margin: auto 0 " @mouseover="banner(show = 2)">
        <div ref="frame_2" class="frame_border frame">
          <div class="hover_div">
            <div class="hover_div_img">
              <img ref="img_2" draggable="false" src="../../../../assets/cevc.png" alt="cevc" class="hover_img" />
            </div>
            <div class="hover_mid">
              <a ref="word_2" class="mar">中国新能源汽车大赛</a>
            </div>
            <div ref="more_2" class="more">
              <a class="mar">了解更多</a>
            </div>
          </div>
        </div>
      </div>
      <div class="car_frame frame" style="margin: auto auto auto 0" @mouseover="banner(show = 3)">
        <div ref="frame_3" class="frame_border frame last">
          <div class="hover_div">
            <div class="hover_div_img">
              <img ref="img_3" draggable="false" src="../../../../assets/catv.png" alt="cevc" class="hover_img img_c" />
            </div>
            <div class="hover_mid">
              <a ref="word_3" class="mar">中国全地形车锦标赛</a>
            </div>
            <div ref="more_3" class="more">
              <a class="mar">了解更多</a>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>




<script>
  import img1 from '../../../../assets/ccpc_bac.jpg'
  import img2 from '../../../../assets/cevc_bac.jpg'
  import img3 from '../../../../assets/catv_bac.jpg'

  export default {
    name: 'Car_match',
    data(){
      return{
        show: 1,
        height:'',
        background: img1,
        size: '100% auto'
      }
    },
    methods:{
      getHeight(){
        this.height = window.innerHeight + 'px';
      },
      banner(index){

        // 初始化
        for (let i = 1;i < 4;i ++) {
          this.$refs['frame_' + i].style.backgroundColor = '';
          this.$refs['word_' + i].style.color = 'rgb(251, 87, 31)';
          this.$refs['more_' + i].style.height = '0px';
          this.$refs['img_' + i].style.width = '0px';
        }
        switch (index) {
          case 1:
            this.size = '100%';
            this.background = img1;
            break;
          case 2:
            this.size = '100% auto';
            this.background = img2;
            break;
          case 3:
            this.size = 'cover';
            this.background = img3;
            break;
        }
        this.$refs['frame_' + index].style.backgroundColor = 'rgba(251, 87, 31, 0.7)';
        this.$refs['word_' + index].style.color = 'white';
        this.$refs['more_' + index].style.height = '26px';
        this.$refs['img_' + index].style.width = '100%';
      }

    },
    created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();
    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }
  };
</script>

<style scoped>
  .mar{
    margin: auto;
  }
  .car_bac{
    display: flex;
    width: 100%;
    height: 100%;
    background-size:cover;
  }
  .background{
    display: flex;
    height: 100%;
    width: 100%;
    background-color: rgba(255,255,255,0.4);
  }
  .car_frame{
    height: 100%;
    width: 27%;
    /*background-color: #fb571f;*/
  }
  .frame_border{
    display: flex;
    height: 100%;
    width: 100%;
    /*border-right: 1px solid rgba(251, 87, 31 ,0.4);*/
  }
  .last{
    border: none
  }
  .frame{
    transition: background-color 400ms;
  }
  .hover_div{
    text-align: center;
    height: 200px;
    width: 300px;
    font-weight: bolder;
    font-size: 26px;
    color: rgb(251, 87, 31);
    /*background-color: #007aff;*/
    margin: auto;
  }
  .hover_mid{
    display: flex;
    height: 80px;
    width: 100%;
    margin: 15px auto 0 auto;
  }
  .hover_div_img{
    height: 70px;
    width: 200px;
    margin: auto;
  }
  .hover_img{
    height: auto;
    width: 0;
    margin: auto;
    transition: width 400ms;
  }
  .img_c{
    margin-top: -57px;
  }
  .more{
    display: flex;
    height: 0;
    width: 94px;
    margin: 0 auto;
    font-size: 14px;
    font-weight: bolder;
    color: white;
    border: 1px solid white;
    border-radius: 50px;
    overflow: hidden;
    transition: height 400ms;
  }
  .more:hover{
    cursor: pointer;
    background-color: white;
    color: rgb(251, 87, 31);
  }
</style>



