      <div class="frame_table table_b"  v-show = "show_1" @click="turn(from = 1,index = 3,to = 'Twice')"><a class="mar" >企业文化</a></div>
      <div class="frame_table table_b"  v-show = "show_1" @click="turn(from = 1,index = 4,to = 'Twice')"><a class="mar" >荣誉认证</a></div>
