    width: 18px;
  }
  .header_bac{
    position: fixed;
    top: 0;
    display: flex;
    height: 40px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
  }

  .header_bac_frame{
    height: 100%;
    width: 95px;
    margin: auto 0 auto 20px;
    font-size: 14px;
  }
  .header_bac_frame:hover{
