  }
  .table_b:hover{
    transition: box-shadow 200ms ,background-color 200ms;
    background-color: rgba(251, 87, 31, 0.8);
    box-shadow: 0 0 20px rgba(251, 87, 31, 0.8);
  }
  .table_hover:hover{
    transition: box-shadow 200ms ,background-color 200ms;
    background-color: rgba(251, 87, 31, 0.8);
    box-shadow: 0 0 20px rgba(251, 87, 31, 0.8);
  }


  .header_bac_right{
    display: flex;
    height: 100%;
    width: 150px;
    margin: auto 0;
  }
  .qub{
    display: flex;
    height: 30px;
    width: 30px;
    margin: auto 3px;
    background-color: #191919;
  }
  .qub:hover{
    cursor: pointer;
    background-color: #3e3e3e;
  }
  .qub_2{
    display: flex;
    height: 30px;
    width: 30px;
    margin: auto 5px auto 3px;
    color: black;
    font-weight: bolder;
    font-size: 14px;
    background-color: #ffffff;
  }
  .qub_2:hover{
    color: rgba(251, 87, 31, 1);
    cursor: pointer;
  }
  .qub_img{
    height: 90%;
    width: 90%;
    margin: auto;
  }

</style>



