import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/home/Home';
import Twice from '@/components/pc/common/secend_page/secend_page';
import Third from '@/components/pc/common/third_page/third_page';
import mobile from '@/components/mobile/mobile';
import Footer from '@/components/pc/common/footer/Footer';
import Header from '@/components/pc/common/header/Header';
import Link from '@/components/pc/common/link/Link';
import Home_page from '@/components/pc/common/home_page/Home_page';
import Car_match from '@/components/pc/common/car_match/car_match';
import Job_All from '@/components/pc/common/Job_All/Job_All';


Vue.component('mobile', mobile);
Vue.component('HeaderBar', Header);
Vue.component('Footer', Footer);
Vue.component('Link', Link);
Vue.component('Home_page', Home_page);
Vue.component('Twice', Twice);
Vue.component('Third', Third);
Vue.component('Car_match', Car_match);
Vue.component('Job_All', Job_All);

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/mobile',
      name: 'mobile',
      component: mobile,
    },

    { // 背景页
      path: '/',
      name: 'Home',
      component: Home,
    },
    { // 首页的swiper
      path: '/home_page',
      name: 'home_page',
      component: Home_page,
    },
    { // 二级页面
      path: '/twice',
      name: 'Twice',
      component: Twice,
    },
    { // 三级页面
      path: '/third',
      name: 'Third',
      component: Third,
    },
    { // #|#|# 那页
      path: '/car_match',
      name: 'Car_match',
      component: Car_match,
    },
    { // 业务总揽
      path: '/Job_All',
      name: 'Job_All',
      component: Job_All,
    },
  ],
});



